import Calendar from './util.js';
import uniCalendarItem from './uni-calendar-item.vue';
import wPicker from "@/components/w-picker/w-picker.vue";
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	components: {
		uniCalendarItem,
		wPicker
	},
	props: {
		/**
		 * 当前日期
		 */
		date: {
			type: String,
			default: ''
		},
		/**
		 * 打点日期
		 */
		// selected: {
		// 	type: Array,
		// 	default () {
		// 		return []
		// 	}
		// },
		/**
		 * 是否开启阴历日期
		 */
		lunar: {
			type: Boolean,
			default: false
		},
		/**
		 * 开始时间
		 */
		startDate: {
			type: String,
			default: ''
		},
		/**
		 * 结束时间
		 */
		endDate: {
			type: String,
			default: ''
		},
		/**
		 * 范围
		 */
		range: {
			type: Boolean,
			default: false
		},
		/**
		 * 插入
		 */
		insert: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			lastIndex: 1,
			currentIndex: 1,
			show: false,
			preweeks: [],
			nextweeks: [],
			weeks: [],
			weekHeader: ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"],
			precalendar: {},
			calendar: {},
			nextcalendar: {},
			nowDate: '',
			aniMaskShow: false
		}
	},
	computed: {
		...mapState(['selected'])
	},
	watch: {
		// selected(newVal) {
		// 	console.log("selected");
		// 	// this.nowDate = this.calendar = this.cale.getInfo(newVal.date)
		// 	// this.cale.setSelectInfo(this.nowDate.fullDate, newVal)
		// 	// this.preweeks = this.cale.preweeks
		// 	// this.nextweeks = this.cale.nextweeks
		// 	// this.weeks = this.cale.weeks
		// }
	},
	created() {
		// 获取日历方法实例
		this.cale = new Calendar({
			date: this.date,
			selected: this.selected,
			startDate: this.startDate,
			endDate: this.endDate,
			range: this.range,
		})
		this.init(this.cale.date.fullDate)


	},
	methods: {
		...mapActions(['getCurrentMonthSelected']),
		// 取消穿透
		clean() {},
		init(date) {
			this.months = this.cale.months;

			this.weeks = this.cale.weeks
			this.nowDate = this.calendar = this.cale.getInfo(date)
			this.$nextTick(function() {
				this.preweeks = this.cale.preweeks
				this.nextweeks = this.cale.nextweeks
				this.precalendar = this.calendar
				this.nextcalendar = this.calendar
			})
		},
		open() {
			this.show = true
			this.$nextTick(() => {
				this.aniMaskShow = true
			})
		},
		close() {
			this.aniMaskShow = false
			this.$nextTick(() => {
				setTimeout(() => {
					this.show = false
				}, 300)
			})
		},
		// confirm() {
		// 	this.setEmit('confirm')
		// 	this.close()
		// },
		change(weeks) {
			this.setEmit('change', weeks)
		},
		setEmit(name, weeks) {
			console.log(weeks)
			let {
				year,
				month,
				date,
				fullDate,
				lunar,
				extraInfo
			} = weeks
			this.$emit(name, {
				range: this.cale.multipleStatus,
				year,
				month,
				date,
				fulldate: fullDate,
				lunar,
				extraInfo: extraInfo || {}
			})
		},
		choiceDate(weeks) {
			// console.log(weeks)
			if (weeks.disable) return

			// this.$nextTick(function() {
			if (this.currentIndex == 0)
				this.precalendar = weeks
			if (this.currentIndex == 1)
				this.calendar = weeks
			if (this.currentIndex == 2)
				this.nextcalendar = weeks
			this.change(weeks)
			// });
		},
		backtoday() {
			this.setDate(new Date())
		},
		pre() {
			const {
				fullDate,
				year,
				month,
				date,
				day
			} = this.cale.getDate(this.nowDate.fullDate, -1, 'month')

			const preDate = new Date(year, month - 1, 1)
			this.setDate(preDate)
		},
		next() {
			const {
				fullDate,
				year,
				month,
				date,
				day
			} = this.cale.getDate(this.nowDate.fullDate, +1, 'month')

			const nextDate = new Date(year, month - 1, 1)
			this.setDate(nextDate)
		},
		setDate(date) {
			var preweeks = []
			var nextweeks = []
			var weeks = []
			this.cale.setDate(date)

			if (this.currentIndex == 1) {
				weeks = this.cale.weeks
				this.nowDate = this.calendar = this.cale.getInfo(date)
				this.weeks = weeks
				this.$nextTick(function() {
					this.change(this.calendar)
					preweeks = this.cale.preweeks
					nextweeks = this.cale.nextweeks
					this.preweeks = preweeks
					this.nextweeks = nextweeks
				})
			} else if (this.currentIndex == 0) {
				preweeks = this.cale.weeks

				this.nowDate = this.precalendar = this.cale.getInfo(date)
				this.preweeks = preweeks
				this.$nextTick(function() {
					this.change(this.precalendar)
					nextweeks = this.cale.preweeks
					weeks = this.cale.nextweeks
					this.weeks = weeks
					this.nextweeks = nextweeks
				})
			} else if (this.currentIndex == 2) {
				nextweeks = this.cale.weeks
				this.nowDate = this.nextcalendar = this.cale.getInfo(date)
				this.nextweeks = nextweeks
				this.$nextTick(function() {
					this.change(this.nextcalendar)
					weeks = this.cale.preweeks
					preweeks = this.cale.nextweeks
					this.weeks = weeks
					this.preweeks = preweeks
				})
			}

			this.$nextTick(function() {
				console.log(date)
				if (date >= new Date('2019-10-01 0:00'))
					this.getCurrentMonthSelected(1)
				else
					this.getCurrentMonthSelected(0)
				this.cale.selected = this.selected
			})
		},
		swiperChange(e) {
			let index = e.target.current;
			// console.log(e);

			let isPrev = (index - this.lastIndex == -1 || index - this.lastIndex == 2) ? true : false;
			this.currentIndex = index;
			if (isPrev) {
				this.pre();
			} else {
				this.next();
			}
			this.lastIndex = index;
		},
		onConfirm(val) {
			const selectDate = new Date(val.checkArr[0] + '-' + val.checkArr[1] + '-01');
			this.setDate(selectDate)
		},
		showPicker() {
			this.$refs["yearMonth"].show();
		},
	}

}
